.works{
    padding: o 3rem o 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
.w-right{
    position: relative;
}
.w-mainCircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}

.w-mainCircle>*{
    position: absolute;
}
.w-mainCircle>:nth-of-type(1){
    top: 5rem;
    left: -3rem;
}
.w-mainCircle>:nth-of-type(2){
    top: -3rem;
    left: 5rem;
}
.w-mainCircle>:nth-of-type(3){
    top: 5rem;
    left: 6rem;
}
.w-mainCircle>:nth-of-type(4){
    top: 5rem;
    left: 15rem;
}
.w-mainCircle>:nth-of-type(5){
    top: 13rem;
    left: 6rem;
}
.w-secCircle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
}
.w-secCircle>img{
    transform: scale(0.6);
}
.w-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.blueCircle{
    background: #194988;
    left: 18rem;
    top: 0rem;
}
.yellowCircle{
    background: #f5c32c;
    left: 18rem;
    top:8rem;
}